<template>
  <div class="summarizesList">

    <div class="type">
      <h1>民主评议列表</h1>
    </div>

    <el-table
      :data="tableData"
      border
      column-key
      size="mini"
      style="width: 90% ;margin: 0 auto">
      <el-table-column
        type="index"
        width="50">
      </el-table-column>
      <el-table-column
        label="描述"
        width="250">
        <template slot-scope="scoped">
          <router-link :to="{path:'/discussMsg',query:{id:scoped.row._id}}">{{scoped.row.title}}</router-link>
        </template>
      </el-table-column>
      <el-table-column
        label="状态"
        width="150">
        <template slot-scope="scope">
          <el-tag size="medium" v-if="scope.row.state===0" type="info">未开启</el-tag>
          <el-tag size="medium" v-if="scope.row.state===1" type="primary">正在进行</el-tag>
          <el-tag size="medium" v-if="scope.row.state===2" type="danger">已结束</el-tag>
        </template>
      </el-table-column>
      <el-table-column
        prop="createdAt"
        label="时间"
        width="250">
      </el-table-column>
      <el-table-column
        label="操作">
        <template slot-scope="scoped">
          <el-button @click="changeIndex(scoped.row._id)" v-if="scoped.row.state!==2" type="primary"  size="small">修改</el-button>
          <el-button @click="updateMsg(scoped.row._id,scoped.row.state)" v-if="scoped.row.state===0" type="info"  size="small">开启
          </el-button>
          <el-button @click="updateMsg(scoped.row._id,scoped.row.state)" v-if="scoped.row.state===1" type="danger"  size="small">结束
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  export default {
    name: "list",
    data() {
      return {
        tableData: [],
        item: {}
      };
    },
    methods: {
      changeIndex(val) {
        this.$router.push({path:"/discuss/editor",query:{id:val}})
      },
      updateMsg(a, b) {
        let id = a;
        let state = b+1;
        let params = {id, state};
        this.$axios.post("updateDiscuss", params).then(res => {
          if (res.code == 200) {
            this.getMsg();
            this.$message({
              message: res.msg,
              type: 'success'
            });
          } else {
            this.$message.error(res.msg);
          }
        });
      },
      getMsg() {
        this.$axios.get("getDiscuss").then(res => {
          this.tableData = res.data;
          // for (let i = 0; i < this.tableData.length; i++) {
          //   if (this.tableData[i].passTime && this.tableData[i].passTime != null) {
          //     var timestamp3 = this.tableData[i].passTime;
          //     var newDate = new Date();
          //     newDate.setTime(timestamp3);
          //     this.tableData[i].passTime = newDate.toLocaleString()
          //   }
          // }
        });
      }
    },
    mounted() {
      this.getMsg();
    }
  };
</script>

<style scoped>
  h1 {
    height: 40px;
    text-align: center;
    font-weight: 500;
    font-size: 20px;
    line-height: 40px;
  }

  .type {
    width: 90%;
    height: 40px;
    margin: 0 auto;
    padding-bottom: 20px;
  }

  .titleImg {
    width: 80px;
    height: 50px;
  }

  .footer {
    text-align: center;
  }

  .img {
    display: block;
    width: 300px;
    height: 300px;
    margin: 0 auto;
  }

  #content {
    width: 60%;
    margin: 20px auto;
  }

  .input {
    width: 200px;
    height: 30px;
    padding: 0;
    margin-left: 10px;
  }

  #radio {
    margin-left: 10px;
  }
</style>



